import React from 'react'
import { replace } from 'ramda'
import { Row, Col, Tooltip } from 'antd'
import { ColumnProps } from 'antd/lib/table'
import { EyeOutlined, DeleteOutlined } from '@ant-design/icons'
import { Page } from '@/components/layout'
import Button from '@/components/button'
import Table from '@/components/query-table'
import history from '@/utils/history'
import useTitle from '@/hooks/useTitle'
import { useReportTemplates, useReportPreview } from '@/hooks/useReportTemplate'
import { useBreadcrumb } from '@/hooks/useApp'
import useModal from '@/hooks/useModal'
import * as routes from '@/constants/routes'
import { ReportTemplate } from '@/services/reportService'
import ReactDndProvider from '@/components/snippet-scrollable-container'
import SnippetPreviewModal from '@/components/snippet-preview-modal'

export default function ReportTemplateList() {
  useTitle('')
  useBreadcrumb([{ name: '' }, { name: '' }])

  const [dataSource] = useReportTemplates()

  const [previewModalVisible, handlePreviewModalVisible, { item }] = useModal({
    content: dataSource.content,
  })

  const [, setIsPreview] = useReportPreview()

  const handleTo = (pathname: string) => () => {
    history.push(pathname)
  }

  const handleToEditPage = (record: ReportTemplate) => () => {
    handleTo(replace(':id', `${record.id}`)(routes.REPORT_TEMPLATE_EDIT))()
  }

  const handlePreview = (evt: React.MouseEvent<HTMLElement>) => {
    setIsPreview()
    handlePreviewModalVisible(evt)
  }

  const columns: ColumnProps<ReportTemplate>[] = [
    {
      title: '',
      dataIndex: 'name',
      render: (v, r) => (
        <Button type="link" onClick={handleToEditPage(r)}>
          {v}
        </Button>
      ),
    },
    {
      title: '',
      dataIndex: 'author',
    },
    {
      title: '',
      dataIndex: 'statusName',
    },
    {
      title: '',
      dataIndex: 'createdAtStr',
    },
    {
      title: '',
      dataIndex: 'updatedAtStr',
    },
    {
      title: '',
      dataIndex: 'description',
    },
    {
      title: 'actions',
      dataIndex: 'delete',
      align: 'center',
      render: (_, r) => (
        <>
          <Tooltip title="">
            <EyeOutlined
              data-id={r.id}
              className="c-icon text-lg cursor-pointer mr-3"
              onClick={handlePreview}
            />
          </Tooltip>
          <Tooltip title="">
            <DeleteOutlined
              data-id={r.id}
              className="c-icon text-lg cursor-pointer"
            />
          </Tooltip>
        </>
      ),
    },
  ]

  return (
    <ReactDndProvider className="is--preview">
      {previewModalVisible && (
        <SnippetPreviewModal
          snippetList={item?.setting}
          onCancel={handlePreview}
        />
      )}
      <Page style={{ minHeight: 'calc(100vh - 203px)' }}>
        <Row className="mb-4" justify="center">
          <Col span={20} className="flex justify-end">
            <Button color="outline"></Button>
            <Button
              color="outline"
              className="ml-4"
              onClick={handleTo(routes.REPORT_TEMPLATE_CREATE)}>
              
            </Button>
          </Col>
        </Row>
        <Row justify="center">
          <Col span={20}>
            <Table rowKey="id" columns={columns} dataSource={dataSource} />
          </Col>
        </Row>
      </Page>
    </ReactDndProvider>
  )
}
